/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
	background: #f6f6f6;
	// color: #666;
	border: 1px solid $color__border;
	// border-radius: 3px;

	&:focus {
		color: #111;
	}
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
	padding: 3px;
	background: #f6f6f6;
	border: 1px solid $color__border;
}

textarea {
	padding-left: 3px;
	width: 100%;
}


/* Blog Posts Comments Area */

.single-post {
		.comment {
		padding-top: $base__space-unit;
	}

	.comments-link a {
		color: white;

		&:hover {
			color: $color__link-hover;
		}
	}

	.comments-title, .comment-reply-title {
		font-weight: bold;
		font-size: 2rem;
	}
	.comment-author-avatar {
		width: 64px;
		height: 64px;
		border-radius: 50%;
    margin-right: $base__space-unit;
    overflow: hidden;
	}

	.comment-author {
		font-weight: bold;
	}

	ol.comment-list {
		padding-left: 0;
		margin-left: 0;
	}

	.comment article {
		padding-bottom: $base__space-unit;
	}

	.comment-body {
		display: flex;
	}

	.comment-metadata {
		color: $color__brand-grey;

		a {
			color: $color__brand-green;
		}
	}

	.children li {
    border-top: 1px solid $color__border;
	}

	.comment-list > li {
    border-bottom: 1px solid #e1e1e1;
	}

	.comment-form {
		display: flex;
		flex-wrap: wrap;

		label {
		text-transform: uppercase;
		font-family: $font__headings;
		font-size: 1.1rem;
    margin-bottom: 0.5rem;
	}

	.comment-form-comment {
		width: 100%;
	}

	.comment-form-author {
		width: 49%;
		margin-right: 1%;

		input[id="author"] {
			width: 100%;
		}
	}

	.comment-form-email {
		width: 49%;
		margin-left: 1%;

		input[id="email"] {
			width: 100%;
		}
	}

	.comment-form-url {
		width: 100%;

		input[id="url"] {
			width: 100%;
		}
	}
}
	
	
	input[type=submit] {
		background: $color__brand-green;
    color: #fff;
    cursor: pointer;
    font-family: $font__headings;
    font-size: 0.9rem;
    line-height: 1;
    padding: .75rem 1.5rem;
    text-decoration: none;
    text-transform: uppercase;
		border-color: none;
		border-radius: 0;
		box-shadow: none;
		text-shadow: none;

		&:hover {
			background: #15544f;
		}
	}
}

.required {
	color: $color__brand-orange;
}
